___CSS_LOADER_EXPORT___.push([module.id, `.calendar {
    width: 100%;
    height: 100%;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 8px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.calendar-header button {
    background: none;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
}

.calendar-grid {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: auto repeat(6, 1fr);
    gap: 4px;
    width: 100%;
}

.calendar-day {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.9rem;
    box-sizing: border-box;
    position: relative;
}

.calendar-day.header {
    font-weight: bold;
    /* background-color: #f0f0f0; */
    cursor: pointer;
}

.calendar-day.outside {
    color: #aaa;
}

.calendar-day.today {
    /* background-color: red; */
    background-color: #007bff;
    color: white;
    border-radius: 8px;
}

.calendar-day.selected-day {
    border: 2px solid #007bff;
    border-radius: 8px;
}

.day-state-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    position: absolute;
    bottom: 4px;
}
